@import '../../common/colors.scss';

.header {
    background-color: $secondary-color;
    height: 70px;
    padding: 0px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    color: $font-primary;
    font-size: 20px;
    font-weight: 600;
}

.user-image img {
    width: 38px;
    height: 38px;
}

.search-bar {
    width: 50%;
    display: flex;
    justify-content: center;
    form {
        display: flex;
        justify-content: center;
        width: 70%;
        button {
            padding: 0px 8px;
            font-size: 20px;
            cursor: pointer;
            height: 38px;
        }
    }
    input {
        font-size: 18px;
        width: 100%;
        padding: 5px 5px 5px 10px;
        height: 38px;
        outline: none;
    }
}